li {
  display: flex;
  justify-content: space-between;
  align-items: center; /* Centra verticalmente los elementos dentro del flexbox */
  width: 25%;
  height: 72px;
  margin: 12px auto 0;
  background-color: white;
  font-family: "Ubuntu Mono", monospace;
  font-size: 16px;
  position: relative;
  border-radius: 4px;
}

li p {
  color:black;
  font-size:24px;
  background:transparent;
  margin: auto;
  text-align: center; /* Centra horizontalmente el texto del elemento <p> */
}

li span:first-child {
  font-size:2em;
  background:transparent;
  position: absolute;
  right: 0.25em;
}

li span:last-child {
  font-size:2em;
  /* color:black; */
  background:transparent;
  position: absolute;
  left: 0.25em;
}
.TodoItem{
  width: 75%;
  background-color:#fafafa;
  position:relative;
  display:flex;
  justify-content:center;
  align-items:center;
  margin-top:24px;
  box-shadow:0 5px 50px rgba(32,35,41,.15)
  }
  .TodoItem-p{
  margin:24px 0 24px 24px;
  width:calc(100% - 120px);font-size:18px;
  line-height:24px;font-weight:400
  }
  .TodoItem-p--complete{
  text-decoration:line-through
  }

